body{
    margin: 30px 30px 30px 30px;
    font-family: 'Open Sans', sans-serif;
    color: white;
    background: -webkit-gradient(linear, right top, left top, from(#da4453), to(#89216b));
    background: -webkit-linear-gradient(right, #da4453, #89216b);
    background: -o-linear-gradient(right, #da4453, #89216b);
    background: linear-gradient(270deg, #da4453, #89216b);
    background-size: 400% 400%;

    -webkit-animation: Animation 9s ease infinite;
    animation: Animation 9s ease infinite;
}
p{
    color: white;
}
a:link 
{ 
text-decoration:none; 
} 

@-webkit-keyframes Animation {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes Animation { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}


