.icon-menu,
.icon-menu.open {
  display: inline-block;
  cursor: pointer;
  position: absolute;
  right: 30px;
  top: 30px;
  padding-top: 8px;
  z-index: 9999;
  border-radius: 25px;
}

.icon-menu 
.bar1,
.bar2,
.bar3 {
  width: 25px;
  height: 3px;
  background-color:white;
  margin: 5px 0;
  border-radius: 20px;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}

.icon-menu.open .bar1 {
  -webkit-transform: rotate(-45deg) translate(-4px, 4px);
  -ms-transform: rotate(-45deg) translate(-4px, 4px);
      transform: rotate(-45deg) translate(-4px, 4px);
}

.icon-menu.open .bar2 {
  opacity: 0;
}

.icon-menu.open .bar3 {
  -webkit-transform: rotate(45deg) translate(-6px, -6px);
  -ms-transform: rotate(45deg) translate(-6px, -6px);
      transform: rotate(45deg) translate(-6px, -6px);
}
.icon-menu.open div{
    background-color:#333;
}

/* Menu */

.menu {
  width: 100%;
  display: block;
  text-align: center;
  top: 0px;
  padding: 0px;
}
.menu ul {
  position: relative;
  list-style: none;
  top: 0px;
  font-size: 24px;
  padding: 0px;
}
.menu li {
  list-style: outside none none;
  margin: 10px 0px;
  padding: 0;
  cursor: pointer;
}
li:hover{
  font-style: italic;
}
a{
    color: #333;
}




